/* SentimentPieChart.css */

.sentiment-pie-chart {
    float: left;
    width: 48%; /* Adjust width as needed */
    height: 450px;
    margin: 20px 1%; /* Add margin to separate from the WordCloud */
    padding: 35px;
    background-color: #ffffff;
    border-radius: 20px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
        transform: scale(1.05);
        box-shadow: 0 12px 24px rgba(0, 0, 0, 0.3);
    }
}
